import { Form } from "react-bootstrap"

const MonthSelectBox = () => {
    return (
        <Form.Control as='select' custom required>
            <option>01</option>
            <option>02</option>
            <option>03</option>
            <option>04</option>
            <option>05</option>
            <option>06</option>
            <option>07</option>
            <option>08</option>
            <option>09</option>
            <option>10</option>
            <option>11</option>
            <option>12</option>
        </Form.Control>
    )
}

export default MonthSelectBox